@import "../../../styles/sass/mixins";

.language {
  @include contentBlock();
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;

  .title {
    @include text($fontSize: 18px, $fontWeight: 700);
  }
}

.flag {
  width: 100%;
  height: 100%;
  background: center/cover no-repeat;
  border-radius: 50%;
}